export class Settings  {
    public UserId: string = "";
    public Username: string = "";
    public Password: string = "";
    public EmailAddress: string = "";
    public FullName: string = "";
    public DepartmentId: number = 0;
    public AuthToken: string = "";
    public Language: string = "";
    public AuthTokenExpiry: string = "";
    public EnableDetailedResponses: boolean = true;
    public EnablePushNotifications: boolean = false;
    public PushDeviceUriId: string = "";
    public PersonnelFilter: string = "";
    public UnitsFilter: string = "";
    public DepartmentName: string = "";
    public DepartmentCreatedOn: string = "";
    public SortRespondingTop: boolean = false;
    public EnableCustomSounds: boolean = false;
    public CustomRespondingText: string = "";
    public EnableGeolocation: boolean = true;
    public EnableSounds: boolean = false;
    public SelectedGroup: string = "0";
}


